<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <article class="mb-8 mb-md-15">
    <!--  -->
    <h2 class="visually-hidden">ALBUMS</h2>

    <!--  -->
    <ScrollingText />

    <!-- Albums -->
    <div class="container my-12 my-md-15">
      <ul class="row justify-content-center justify-content-md-start row-gap-11">
        <li v-for="(album, index) in albums" :key="album.id" class="col-10 col-md-6 col-xl-3">
          <AlbumCard :album="album" :index="index" />
        </li>
      </ul>
    </div>

    <div class="container my-12 my-md-15">
      <h3 class="text-center mb-4">Swiper </h3>
      <!-- <Swiper
        :modules="swiperModules"
        :navigation="true"
        :spaceBetween="24"
        :direction="albumSwiper.direction"
        :slidesPerView="albumSwiper.slidesPerView"
        :loop="albumSwiperLoop"
        :autoplay="{
          delay: 3000,
        }"
        :class="albumSwiper.class"
      > -->
      <Swiper
        :modules="swiperModules"
        :navigation="true"
        :spaceBetween="24"
        :direction="albumSwiper.direction"
        :slidesPerView="albumSwiper.slidesPerView"
        :loop="albumSwiperLoop"
        :class="albumSwiper.class"
      >
        <SwiperSlide
          v-for="(album, index) in albums"
          :key="album.id"
          class="col-10 col-md-6 col-xl-3"
        >
          <AlbumCard :album="album" :index="index" />
        </SwiperSlide>
      </Swiper>
    </div>

    <!--  -->
    <ScrollingText :isReverse="true" />
  </article>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { AlbumRecord } from "@/services/data/album";
import { useAlbums } from "@/composables/data/useAlbums";
import ScrollingText from "@/components/home/albums/ScrollingText.vue";
import AlbumCard from "@/components/home/albums/AlbumCard.vue";

/* Swiper  */
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";
// Import required modules
import { Navigation, Pagination, Scrollbar, Autoplay } from "swiper/modules";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";

// TODO 
export default {
  /**
   * @typedef {Object} AlbumsData
   * @property {AlbumRecord[]} albums 
   */
  /** @returns {AlbumsData} */
  data() {
    return {
      albums: [], // 
      swiperModules: [Navigation, Pagination, Scrollbar, Autoplay], // Swiper 
      albumSwiper: {
        slidesPerView: 1, // 
        // direction: "horizontal", // 
        direction: "vertical", // 
        class: ["vertical"],
        // class: [],
      },
    };
  },

  created() {
    // 
    useAlbums().then(({ albums }) => {
      // TODO TEST
      console.log(`albums ==>`, albums.value);
      // this.albums = albums;
      this.albums = albums.value.slice(0, 5);

      //  Swiper 
      this.updateSwiperByWidth();
    });
  },

  mounted() {
    //  Swiper 
    window.addEventListener("resize", this.updateSwiperByWidth);
  },

  methods: {
    // TODO  4 
    /**
     *  Swiper
     */
    updateSwiperByWidth() {
      // 
      if (window.matchMedia("(max-width: 768px)").matches) {
        // this.albumSwiper.slidesPerView = 1;
        this.albumSwiper.slidesPerView = 4;
      }
      // 
      else if (window.matchMedia("(max-width: 1200px)").matches) {
        this.albumSwiper.slidesPerView = 2;
      }
      // 
      else {
        this.albumSwiper.slidesPerView = 4;
      }
    },
  },
  // method

  computed: {
    /**
     * 
     * @returns {boolean} 
     */
    albumSwiperLoop() {
      //    
      return this.albums.length > this.albumSwiper.slidesPerView;
    },
  },

  components: {
    ScrollingText,
    AlbumCard,
    Swiper,
    SwiperSlide,
  },
};
</script>

<style scoped lang="scss">
@use "@/assets/scss/mixins/rwd";

//  Swiper 
.swiper {
  /*
    Swiper 
    padding 
  */
  padding-top: 25px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;

  //  100%
  @include rwd.pad-down {
    width: 89%;
  }

  //  Swiper
  &.vertical {
    height: 1566px;
  }
}

//  Swiper  slide 
.swiper-slide {
  font-size: 30px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
