import Swal from "sweetalert2";

/**  */
export class AlertOptions {
  /**  */
  static get base() {
    return {
      position: "top", // 
      confirmButtonText: "", // confirm button 
    };
  }

  /**  */
  static get success() {
    return {
      icon: "success", // 
    };
  }

  /**  */
  static get error() {
    return {
      //  class
      customClass: {
        title: "text-danger",
        // htmlContainer: "text-danger",
      },

      icon: "error", // 
      confirmButtonColor: "var(--bs-danger)", //  confirm button 
      confirmButtonText: "", // confirm button 
    };
  }

  /**  ( ESC ) */
  static get lock() {
    return {
      allowOutsideClick: false, // 
      allowEscapeKey: false, //  ESC 
    };
  }

  /**  Enter  */
  static get defocusButton() {
    return {
      //  didOpen 
      didOpen(popup) {
        popup.focus();
      },
    };
  }

  /**
   * 
   * 
   */
  static get forceFocus() {
    return {
      ...this.lock,
      ...this.defocusButton,
    };
  }

  /**
   * Timer
   * 
   */
  static get timer() {
    return {
      showConfirmButton: false, //  confirm button
      timer: 1200, //  ()
    };
  }
}

/**  */
export class BaseAlert {
  /**
   * 
   */
  static get baseAlert() {
    return Swal.mixin(AlertOptions.base);
  }

  /**
   * 
   */
  static get sucessAlert() {
    return this.baseAlert.mixin(AlertOptions.success);
  }

  /**
   * 
   */
  static get errorAlert() {
    return this.baseAlert.mixin(AlertOptions.error);
  }
}

/**  confirm button  */
export class ConfirmAlert extends BaseAlert {
  /**
   * 
   * @param {string} [msg = ""] 
   * @param {Object} [options = {}]  SweetAlert2 
   */
  static alertSuccess(msg = "", options = {}) {
    this.sucessAlert.fire({
      ...options,
      title: msg,
    });
  }

  /**
   * 
   * @param {string} [msg = ""] 
   * @param {Object} [options = {}]  SweetAlert2 
   */
  static alertError(msg = "", options = {}) {
    this.errorAlert.fire({
      ...AlertOptions.forceFocus,
      ...options,
      title: msg,
    });
  }

  /**
   * 
   * @param {string} [title = ""] 
   * @param {string} [detail = ""] 
   * @param {Object} [options = {}]  SweetAlert2 
   */
  static alertErrorDetail(title = "", detail = "", options = {}) {
    this.errorAlert.fire({
      ...AlertOptions.forceFocus,
      ...options,
      title: title,
      text: detail,
    });
  }
}

/** Timer  */
export class TimerAlert extends BaseAlert {
  /**
   * 
   * @param {string} [msg = ""] 
   * @param {Object} [options = {}]  SweetAlert2 
   */
  static alertSuccess(msg = "", options = {}) {
    this.sucessAlert.fire({
      ...AlertOptions.timer,
      ...options,
      title: msg,
    });
  }

  /**
   * 
   * @param {string} [msg = ""] 
   * @param {Object} [options = {}]  SweetAlert2 
   */
  static alertError(msg = "", options = {}) {
    this.errorAlert.fire({
      ...AlertOptions.timer,
      ...options,
      title: msg,
    });
  }
}
