import { TagApi } from "@/api";
// eslint-disable-next-line no-unused-vars
import { TagRecord } from "./tagRecord";

/**
 * Tag 
 */
export class TagService {
  /**
   *  Tag 
   *  {@link TagApi.fetchTags} 
   * @returns {Promise<Object[]>} 
   */
  static async fetchTags() {
    let result;
    await TagApi.fetchTags()
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });
    return result || [];
  }

  /**
   * 
   * @param {TagRecord[]} tags Tag 
   * @param {string} name 
   * @returns {TagRecord}  Tag 
   */
  static searchByName(tags, name) {
    // 
    let result = tags.find((item) => {
      return item.name === name;
    });

    // 
    if (!result) {
      // 
      result = tags.find((item) => {
        return item.name.includes(name);
      });
    }

    return result;
  }
}
