import { Model } from "../model";
import { EventRecord } from "./eventRecord";

/**
 * 
 */
export class EventModel extends Model {
  /**
   * 
   * @type {EventRecord[]}
   */
  get datas() {
    return this._datas;
  }

  /**
   * 
   * @param {Object[]} [events] Event 
   */
  constructor(events = []) {
    super();

    events.forEach((item) => {
      // 
      const eventRecord = new EventRecord(item);
      // 
      this._datas.push(eventRecord);
    });
  }
}
