import { EventApi } from "@/api";
// eslint-disable-next-line no-unused-vars
import { EventTagRecord } from "./eventTagRecord";
import { TypeUtils } from "@/helpers";

/**
 * Event 
 */
export class EventService {
  /**
   *  index
   */
  static get mainTagIndex() {
    return 0;
  }

  /**
   * 
   *  {@link EventApi.fetchEvents} 
   * @returns {Promise<Object[]>} 
   */
  static async fetchEvents() {
    let result;
    await EventApi.fetchEvents()
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });
    return result || [];
  }

  /**
   * 
   *  {@link EventApi.fetchEvent} 
   * @param {number} id  ID
   * @returns {Promise<Object>} 
   */
  static async fetchEvent(id) {
    let result;
    await EventApi.fetchEvent(id)
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });
    return result || {};
  }

  /**
   *  Banner 
   *  {@link EventApi.fetchBanners} 
   * @returns {Promise<Object[]>} 
   */
  static async fetchBanners() {
    let result;
    await EventApi.fetchBanners()
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });
    return result || [];
  }

  /**
   *  Tag ID 
   * @param {EventTagRecord[]} events 
   * @param {number[]} tagIds  Tag ID 
   * @returns {EventTagRecord[]} 
   */
  static filterByTagIds(events, tagIds) {
    //  events
    if (!TypeUtils.isArrayWithItems(events)) {
      return [];
    }

    //  tagIds
    if (!TypeUtils.isArrayWithItems(tagIds)) {
      return events;
    }

    // 
    return events.filter((event) => {
      for (const id of event.tags) {
        if (tagIds.includes(id)) {
          return true;
        }
      }
      return false;
    });
  }
}
