import { Record } from "../record";

/**
 * Tag 
 */
export class TagRecord extends Record {
  static #props = ["name"];

  /**
   * 
   * @type {string}
   */
  name;

  /**
   * Tag 
   * @param {Object} data Tag 
   */
  constructor(data) {
    super(data);
    this._assignData(data, TagRecord.#props);
  }
}
