import { Model } from "../model";
import { TagRecord } from "./tagRecord";
import { TagService } from "./tagService";

/**
 * Tag 
 */
export class TagModel extends Model {
  /**
   * Tag 
   * @type {TagRecord[]}
   */
  get datas() {
    return this._datas;
  }

  /**
   *  Tag ID 
   * @type {Object<number, TagRecord>}
   * @protected
   */
  _idMap = {};

  /**
   * Tag 
   * @param {Object[]} [tags] Tag 
   */
  constructor(tags = []) {
    super();

    tags.forEach((item) => {
      // 
      const tagRecord = new TagRecord(item);
      // 
      this._datas.push(tagRecord);
      //  ID 
      this._idMap[tagRecord.id] = tagRecord;
    });
  }

  /**
   *  ID  Tag 
   * @param {number} id Tag  ID
   * @returns {string | undefined} Tag 
   */
  getNameById(id) {
    return this._idMap[id]?.name;
  }

  /**
   *  Tag ID
   * @param {string} name 
   * @returns {number | undefined}  Tag ID
   */
  getIdByName(name) {
    return TagService.searchByName(this._datas, name)?.id;
  }

  /**
   * 
   * @param {number} id Tag ID
   * @returns {TagObj} 
   */
  createTagObj(id) {
    return {
      id,
      name: this.getNameById(id),
    };
  }

  /**
   *  TagModel
   * @param {*} model 
   * @returns {boolean}  TagModel  true false
   */
  static isMe(model) {
    return model && Object.getPrototypeOf(model) === this.prototype;
  }
}
