import { createRouter, createWebHashHistory } from "vue-router";
import homeView from "../views/HomeView.vue";
import { SysConstants } from "@/helpers";

const router = createRouter({
  history: createWebHashHistory(import.meta.env.BASE_URL),

  scrollBehavior(to, from, savedPosition) {
    // TODO DELETE
    // console.log(`[Router] to ==>`, to);
    // console.log(`[Router] from ==>`, from);
    // console.log(`[Router] savedPosition ==>`, savedPosition);
    // console.log(`[savedPosition] top ==>`, savedPosition?.top);

    //  position 
    if (savedPosition) {
      // 
      return new Promise((resolve) => {
        setTimeout(() => {
          // console.log(`==============  scroll  ==============`);
          resolve(savedPosition);
        }, SysConstants.scrollWaitingTime);
      });
    }
  },

  routes: [
    // 
    {
      path: "/",
      name: "home",
      component: homeView,
    },
    // {
    //   path: '/events/:tag(.*)?',
    //   name: 'events',
    //   component: () => import("../views/EventListView.vue"),
    //   props: true,
    // },
    // 
    {
      path: "/events",
      name: "eventList",
      component: () => import("@/views/EventListView.vue"),
    },
    // 
    {
      path: "/events/:id",
      name: "eventDetail",
      component: () => import("@/views/EventDetailView.vue"),
    },
    // 
    {
      path: "/test",
      name: "test",
      component: () => import("@/views/test/TestView.vue"),
      children: [
        {
          path: "bs",
          name: "testBs",
          component: () => import("@/views/test/TestBs.vue"),
        },
        {
          path: "layout",
          name: "testLayout",
          component: () => import("@/views/test/TestLayout.vue"),
        },
        {
          path: "js",
          name: "testJs",
          component: () => import("@/views/test/TestJs.vue"),
        },
      ],
    },
    // 404
    {
      path: "/:pathMatch(.*)*",
      name: "NotFound",
      // redirect: "/", // 
      component: () => import("@/views/NotFoundView.vue"), // 
    },
  ],
});

export default router;
