import { EventApi } from "@/api";

/**
 * 
 */
export class AlbumService {
  /**
   * 
   *  {@link EventApi.fetchAlbums} 
   * @returns {Promise<object[]>} 
   */
  static async fetchAlbums() {
    let result;
    await EventApi.fetchAlbums()
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });
    return result || [];
  }
}
