import { Model } from "../model";
import { AlbumRecord } from "./albumRecord";
import { TagModel } from "../tag";

/**
 * 
 * @extends {Model}
 */
export class AlbumModel extends Model {
  /**
   * 
   * @type {AlbumRecord[]}
   */
  get datas() {
    return this._datas;
  }

  /**
   * 
   * @param {object[]} [albums] Album 
   * @param {TagModel} tagModel Tag 
   */
  constructor(albums = [], tagModel) {
    //  albums 
    if (!Array.isArray(albums)) {
      throw new TypeError("[albums] ");
    }

    //  tagModel  TagModel 
    if (!TagModel.isMe(tagModel)) {
      throw new TypeError("[tagModel]  TagModel ");
    }

    super();

    albums.forEach((item) => {
      // 
      const albumRecord = new AlbumRecord(item, tagModel);
      // 
      this._datas.push(albumRecord);
    });
  }
}
