import { Record } from "../record";
import { DateService } from "@/services/dateService";

/**
 * Event 
 */
export class EventRecord extends Record {
  static #props = [
    "date",
    "sTime",
    "eTime",
    "title",
    "subTitle",
    "opening",
    "desc",
    "img",
    "tags",
    "isOnSale",
    "isBanner",
    "bannerDesc",
  ];

  /**
   * 
   * @type {string}
   */
  date;

  /**
   *  {@link DateStyles.dateAbbrMonthYear}
   * @type {string}
   */
  abbrDate;

  /**
   * 
   * @type {string}
   */
  sTime;

  /**
   * 
   * @type {string}
   */
  eTime;

  /**
   * 
   * @type {string}
   */
  title;

  /**
   * 
   * @type {string}
   */
  subTitle;

  /**
   * 
   * @type {string}
   */
  opening;

  /**
   * 
   * @type {string}
   */
  desc;

  /**
   * 
   * @type {string}
   */
  img;

  /**
   *  ID
   * @type {number[]}
   */
  tags;

  /**
   * 
   * @type {boolean}
   */
  isOnSale = false;

  /**
   *  Banner
   * @type {boolean}
   */
  isBanner = false;

  /**
   * Banner 
   * @type {string}
   */
  bannerDesc;

  /**
   * Event 
   * @param {Object} data Event 
   */
  constructor(data) {
    super(data);
    this._assignData(data, EventRecord.#props);

    // 
    if (this.date) {
      this.abbrDate = DateService.formatAsDateAbbrMonthYear(this.date);
    }
  }
}
