import { TypeUtils } from "../typeUtils";

/**
 * 
 */
export class EventBase {
  /**
   * 
   * @type {HTMLElement}
   * @protected
   */
  _elem;

  /**
   * 
   * @param {HTMLElement} elem 
   * @throws  elem
   */
  constructor(elem) {
    if (!TypeUtils.isHtmlElement(elem)) {
      throw Error("[elem]  HTML ");
    }
    this._elem = elem;
  }
}
